/*=====================================================
 Links & Paragraph styles
 ======================================================*/
p { font-family: "Open Sans"; font-weight: 400; font-size: 16px; font-size: 1pxrem; margin-bottom: 0.80902; line-height: 1.61803; }
p.lead { font-size: 20px; font-size: 1.25pxrem; margin-bottom: 18px; }
